import React from "react"

function Poster({data, saatDen}){
  return(
    <div className="Poster">
      {console.log(data)}
      <div className="GorenDiv">
      <p className="CityCountry">{data.result.name}, {data.result.sys.country}</p>
      <p>{saatDen}</p>
    
      
      
        <p className="mainTemp">{(data.result.main.temp -272.15).toFixed()}°C</p>
        <p className="feelsLikeTemp">Feels like:{(data.result.main.feels_like -272.15).toFixed()}°C</p>
     
      
      <div className="WindHumidity">
      <span className="Wind">
        Wind speed:{(data.result.wind.speed * 3.6).toFixed()}km/h
      </span>
  <span className="Humidity">Humidity:{data.result.main.humidity}%</span>
  </div>
     </div>
    </div>
  );
}


export default Poster