import React,{useState, useEffect} from 'react';
import Poster from "./Poster"

const ApiURL="https://api.openweathermap.org/data/2.5/weather?q=";
    const ApiKEY="bf2fc1a7b89b9aca01171b3b42a46b29";
    const denovi = ["Nedela", "Ponedelnik", "Vtornik", "Sreda", "Cetvrtok", "Petok", "Sabota"];


function Result({grad}) {
    const [error, setError] = useState(null);
    const [isLoaded, setIsLoaded] = useState(false);
    const [items, setitems] = useState([]);
    const [date, setDate] =useState ("");

    const fetchURL = ApiURL + grad + "&appid=" + ApiKEY;
    console.log("Result grad:" + grad);
    console.log("DAAa");
   
    const zemiData = () => {
      let today =  new Date();
      let saati = today.getHours();
      let den = denovi[today.getDay()];
      let data = den + ", " + saati; 
      console.log(den);
      setDate=(data);
    };

    useEffect( ()=> {
      fetch(fetchURL)
      .then((re) => {
        if(re.status !== 200) {
          throw new Error();
        } else return re.json()
      })
      .then (
        (result) => {
          console.log(result);
          setIsLoaded(true);
          setitems([{result}]);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
    }, []);

    if(error){
    return (<div className="nemaGrad"><p>Nema takov grad</p></div>);
    }
    else if(!isLoaded) {
      return <div>Loading...</div>
    }
    else {
      return (
        <div >
              
              {items.map(item => <Poster key="1" data={item} saatDen={date}/> )}
    
             
        </div>
      );
    }

     
  
  
}

export default Result;
