import React from "react"

function Search({city, setCity, cityEmpty, setCityEmpty, changeCity}){

  const handleInputChange = (e) => {
    setCity(e.target.value);
    setCityEmpty("");  
  }

  const handleSumbit = (e) => {
    e.preventDefault();
    setCityEmpty(city);
    setCity("");
  }

  return(
  <div className="Form">
    <form onSubmit={handleSumbit}>
      <input
      className="inputText"
      type="text"
      placeholder="Insert city"
      value={city}
      onChange = {handleInputChange}
      >
      </input>
    </form>
  </div>
);

}

export default Search